.profile__name {
    margin: 0;
    padding: 0;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin-bottom: 16px;
    margin-right: 18px;
    overflow: hidden;
    color: white;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  @media screen and (max-width: 907px) {
    .profile__name {
      font-size: 27px;
      line-height: 33px;
      text-align: center;
      margin-bottom: 0;
      margin-right: 10px;
      margin-left: 7px;
      max-width: 100%;
    }
  }