.profile__button {
    padding: 0;
    box-sizing: border-box;
    border-radius: 2px;
    cursor: pointer;
    background-color: black;
    background-position: center;
    background-repeat: no-repeat;
    align-self: center;
    transition: opacity .3s ease;
}

.profile__button:hover {
    opacity: .8;
}