.popup__save-button {
    width: 358px;
    height: 50px;
    margin: 20px auto 31px;
    padding: 0;
    color: white;
    background: black;
    cursor: pointer;
    font-size: 18px;
    line-height: 22px;
    border: 2px solid #000000;
    border-radius: 2px;
}

.popup__save-button:hover {
    opacity: .8;
}

@media screen and (max-width: 505px) {
    .popup__save-button {
        margin-top: 20px;
        width: 84%;
        height: 46px;
        font-size: 14px;
        line-height: 17px;
        
    }
}